// import html2canvas from "html2canvas";
let appId = 'wxb6a8515159de98f6'; // 弘成
// let appId = 'wx084249e61db2c69d';
export default {
  data() {
    return {
     fullHeight: null,
     isPlay: false, // 音乐
     packet: true, // 红包
     money: true,  // 开始数钱页
     idiom: false,  // 成语页
     rule: true, // 弹窗
     hint: false, // 提示
     result: false,  // 结果页
     num:0,  // 分页
     indexTab: null,
     answerRight:0, // 答对总数
     idiomList: [
                  {
                    right:[{},{},{},{}],
                    allright:['鼠','你','有','财'],
                    select:['鼠','你','宝','藏','财','有'],
                    hintSelect: require("../../static/images/hc_hint0.png")
                  },    
                  {
                    right:[{},{},{},{}],
                    allright:['鼠','一','鼠','二'],
                    select:['一','鼠','独','特','二','鼠'],
                    hintSelect: require("../../static/images/hc_hint1.png")
                  },    
                  {
                    right:[{},{},{},{}],
                    allright:['鼠','你','有','福'],
                    select:['运','鼠','气','你','福','有'],
                    hintSelect: require("../../static/images/hc_hint2.png")
                  }   
                ]
    }
  },
  created() {
   this.fullHeight = document.documentElement.clientHeight

    //分享
    post(`${API.encryption}`,{ url : window.location.href,appid:appId}).then((res)=> {
      if (res.success) {
        let data = res.data
        console.log(data)
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: data.appid, // 必填，公众号的唯一标识
          timestamp: data.timestamp, // 必填，生成签名的时间戳
          nonceStr: data.nonceStr, // 必填，生成签名的随机串
          signature: data.signature,// 必填，签名
          jsApiList: ['updateAppMessageShareData','updateTimelineShareData'] // 必填，需要使用的JS接口列表
        })
      }
    })
           
    wx.ready(function(){
       wx.updateAppMessageShareData({ 
        title: '猜金鼠 赢红包 100%中奖', // 分享标题
        desc: '弘成学习中心祝您新春快乐', // 分享描述
