       wx.updateTimelineShareData({ 
        title: '猜金鼠 赢红包 100%中奖', // 分享标题
        link: 'https://xxzx.chinaedu.net/sncs/#/hongcheng', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: 'https://xxzx.chinaedu.net/wx/img/1578626517022.jpg', // 分享图标
        success: function () {
          // 设置成功
        }
      })

      // 苹果手机播放音乐
      setTimeout(()=> {
        var globalAudio=document.getElementById("bghmusic");
        globalAudio.play();
      })
      
    })

    wx.error(function(res){
      console.log('失败')
    })

    // 红包
    // var timer=setInterval(() => {
    //   this.packet = !this.packet
    // }, 600);
  },
  
  // destroyed() {
  //   window.clearInterval(timer)
  // },
  methods: {
    // 播放音乐
    playing() {
      this.isPlay = !this.isPlay
      if(this.isPlay) {
        document.getElementById('bghmusic').pause()
      }else {
        document.getElementById('bghmusic').play()
      }
    },
    start() {
      this.money = false
      this.idiom = true
    },

    handrule(){
      this.rule = false
    },

    // 提示答案
    openhint() {
      this.hint = true
    },
    closehint() {
      this.hint = false
    },
     // 点击确定
    btn() {
      this.hint = false
      let result=[]
      let allright=this.idiomList[this.num].allright
      // let result=this.$get(this.idiomList[this.num].right[isHasId],'name')
      // console.log(this.idiomList[this.num].right)
      this.idiomList[this.num].right.forEach((item,index) => {
        result.push(item.name)
      })
      if(result.toString() == allright.toString()) {
        this.answerRight += 1
      }
        // console.log(result)
      if(result.indexOf(undefined) == -1 && result.indexOf(null) == -1) {
        
        this.idiomList[this.num].right = [{},{},{},{}]
        this.num += 1
        if(this.num >= 3) {
          // this.$set(this.idiomList[this.num].right[isHasId],'name',null)
          // this.$set(this.idiomList[this.num].right[isHasId],'id',-1)
          this.idiom = false
          this.result = true
          this.num = 0
          // return
        }
          
      }
    },

   
    // 未选中
    getFirstIndex () {
      let index = -1;
       this.idiomList[this.num].right.forEach((element,eleIndex) => {
          if (!element.name) {
            if (index === -1) {
               index = eleIndex
            }
          }
      });
      return index
    },

     // 加类名
    activeFn(item,index) {
      let activeId=this.hasName(item,index)
      if(activeId === -1) {
        return false
      }
      return true
    },
    // 拿到当前已选中的索引
    hasName (name,id) {
      let ids = -1
      this.idiomList[this.num].right.forEach((item,index) => {
        if (item.name === name && (item.id === id || item.id === 0)) {
          ids = index
        }
      })
      return ids
    },
    // 是否全部选择
    getSelectTotal () {
      let total = 0;
      this.idiomList[this.num].right.forEach((item,index) => {
        if (item.name && (item.id !== -1 || item.id)) {
          total++
        }
      })
      return total
    },

    select (item,index) {
      this.check = !this.check
     
      // 选中的个数
      let total = this.getSelectTotal()
    
      let isHasId = this.hasName(item,index)
      // console.log(isHasId)
      
      // 已选中删除
      if (isHasId !== -1) {
        this.$set(this.idiomList[this.num].right[isHasId],'name',null)
        this.$set(this.idiomList[this.num].right[isHasId],'id',-1)
        // console.log(this.idiomList[this.num].right[isHasId])
        return
      }
      if (total >= 4) {
        return 
      }

      // 未选择
      let notIndex = this.getFirstIndex()
      // console.log(notIndex)
      this.$set(this.idiomList[this.num].right[notIndex],'name',item)
      this.$set(this.idiomList[this.num].right[notIndex],'id',index)
    },

   
    
    // 继续挑战
    challenge() {
      this.result = false
      this.allright = 0
      this.money = true
    },

    // 分享
    parseQueryString(url) {
      var reg_url = /^[^\?]+\?([\w\W]+)$/,
        reg_para = /([^&=]+)=([\w\W]*?)(&|$|#)/g,
        arr_url = reg_url.exec(url),
        ret = {};
      if (arr_url && arr_url[1]) {
        var str_para = arr_url[1],
          result;
        while ((result = reg_para.exec(str_para)) != null) {
          ret[result[1]] = result[2];
        }
      }
      return ret;
    },
    
  }
};
</script>
